/* faq */
.faq_trigger {
	display: flex;
	justify-content: space-between;
	cursor: pointer;
	font-size: 16px;
	font-weight: bold;
}

.faq_icon {
	font-size: 20px;
	font-weight: bold;
	transition: transform 0.3s ease;
}

.faq_answer-container {
	overflow: hidden;
	transition: all 0.3s ease;
	height: 0;
	opacity: 0;
	transform: scale(0.9);
}

/* remove the bluehighlight */
button {
	-webkit-tap-highlight-color: transparent; /* for WebKit-based browsers */
	tap-highlight-color: transparent; /* for other browsers */
  }

/*Floating Button*/
button.fab {
	cursor: pointer;
	position: fixed;
	bottom: 2rem;
	z-index: 99;
	right: 2rem;
	font-size: 1.5rem;
	color: white;
	background-color: pink;
	width: 3.6rem;
	height: 3.6rem;
	border-radius: 50%;
	box-shadow: 3px 6px 10px rgba(0, 0, 0, 0.2);
	border: none;
  }


  /*Parent Menu Container (ul)*/
  .floatingMenu {
	pointer-events: none;
	position: fixed;
	overflow: hidden;
	z-index: 98;
	bottom: 3.8rem;
	padding: 0.2em 0.4rem 2.4rem 0;
	right: 2rem;
	display: flex;
	flex-direction: column;
  }
  .floatingMenu.open {
	pointer-events: inherit;
  }
  /*Menu Items (li's)*/
  .floatingMenu li {
	opacity: 0;
	top: 20em;
	display: flex;
	align-items: center;
	justify-content: flex-end;
	position: relative;
	column-gap: 1em;
	margin-bottom: 1em;
	transition: top 0.5s ease, opacity 0.5s ease;
  }
  .floatingMenu li.visible {
	opacity: 1;
	top: 0;
  }
  .floatingMenu li:last-of-type {
	margin-bottom: 0;
  }
  /*Menu Item Labels (spans)*/
  .floatingMenu li span {
	padding: 0.4rem;
	opacity: 0;
	font-size: 0.7rem;
	color: white;
	border-radius: 3px;
	background-color: #111111;
	transition: opacity 0.5s ease;
  }
  .floatingMenu li:hover span {
	opacity: 1;
  }
  /*Icons*/
  .floatingMenu li a i {
	color: white;
	display: block;
	font-size: 1.375rem;
	width: 3rem;
	height: 3rem;
	border-radius: 50%;
	text-align: center;
	line-height: 3rem;
	background-color: #222222;
	transition: transform 0.5s ease;
  }
  .floatingMenu li a:hover i {
	transform: scale(1.06);
  }
  
